@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-family: arial;
    font-weight: bold;
    margin: 0px 0px 0px -10px;
    padding-left: 2.5%;
    padding-top: 20px;
    padding-bottom: 15px;
    position: fixed;
    z-index: 100;
    width: 100%;
    top: 0;
}

.topNav {
    float: right;
    padding: 0% 8.5% 0% 0%;
    font-size: 13px;
}
  
.navBar a:hover {
    color: white;
}
#contact-me { 
    /* background-image: url("https://www.colorhexa.com/eeeeee.png"); */
    background-color: #f7f7f7;
    position: absolute;
    width: 100%;
    margin:  -1vw;
    height: 100vh;
    text-align: left;
    
  }
  
  .contactText {
    width: 28%;
    float: left;
    padding: 12% 0% 0% 18%;
    font-weight: lighter;
    display: flex;
    flex-direction: column;
  }

  .contactText img {
      max-height: 90px;
      object-fit: contain;
      margin-left: -55px;
  }
  
  .contactBox {
    width: 49%;
    float: right;
    display: inline-block;
    padding: 12% 1% 0% 0%;
    margin: 0px 0px 5% 2.5%;
  }
  
  #nameBox, #emailBox, #messageBox {
    border: 1px solid grey;
    width: 68%;
    padding: 1.25%;
  }
  
  #nameBox, #emailBox {
    height: 20px;
  }
  
  #messageBox {
    height: 75px;
    display: flex;
    flex-wrap: wrap;
  }
  
  #sendMail {
    text-transform: uppercase;
    letter-spacing: .17em;
    background-color: black;
    color: white;
    border: 1px solid black;
    padding: 10px 20px;
    font-size: 1.1vw;
    margin-top: 15px;
  }
  
  #sendMail:hover {
    background-color: white;
    color: black;
  }
#projects {
    background: -webkit-gradient(linear, left top, right top, color-stop(35%, #f79f99), color-stop(35%, #ffffff));
    background: linear-gradient(90deg, #f79f99 35%, #ffffff 35%);
    /* background: #f7f7f7; */
    min-height: 100vh;
    width: 100%;
}
  
.project-title {
    text-align: center;
    font-size: 45px;
    letter-spacing: .17em;
    padding: 55px 0px 0px 0px;
}

.first-row-project {
  display: flex; 
  flex-direction: row;
  justify-content: space-evenly;
  margin: 5%;
}
  
img {
    max-width: 500px;
    height: 275px; 
    object-fit: cover;
}
  
.first-project-left:hover, .first-project-right:hover {
    opacity: .80;
}
  

#welcome-page {
  min-height: 100vh;
}

.welcomeHeader {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 5vw;
    text-transform: lowercase;
    font-weight: bold;
    letter-spacing: .15em;
}
  
.typeWrite {
  overflow: hidden;
  /* border-right: .15em solid white; */
  white-space: nowrap;
  letter-spacing: .15em;
  -webkit-animation: 
    typing 3.5s steps(40, end);
          animation: 
    typing 3.5s steps(40, end);
}
  
  /* The typing effect */
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
.top {
    background-color: #f79f99;
    padding: 10% 0% .5% 0%;
    margin: -1vw -1vw 0 -1vw;
    justify-content: center;
    display: flex;
    height: 50%;
}
  
.bottom {
    padding: 0% 25% 0% 10%;
    justify-content: center;
    align-items: center;
    display: flex;
}
  
.exploreButton {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1%;
    font-size: 17px;
}
.footer {
    font-size: 25px;
    text-align: right;
    width: 100%;
    font-style: italic;
    position: fixed;
    bottom: 25px;
    width: 100%;
    letter-spacing: .25em;
  }
  
  .linkedin, .github {
    padding: 5px;
  }

  .google {
    padding: 0px 8% 0px 5px;
  }
.App {
  text-align: center;
  font-family: 'Lexend Deca';
  height: 100%;
  scroll-behavior: smooth;
  color: black;
  height: 100%
}

.App-header {
  width: 100%;
  min-height: 100vh;
  /* display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;  */
  /* font-size: calc(10px + 2vmin); */
  color: black;
}

a:link, a:visited, a:active {
  color: black;
  text-decoration: none;
}

a:hover {
  color: grey;
}

